    beforeMount(){
      var wechatShareData = {};
      var _self = this;
      var promise = function (){
        return new Promise(function (resolve, reject) {
          _self.H.toAjax(true).then(function (data) {
            var submitData = data;
            var activityId = _self.$route.query.id;
            submitData.data = {
              activityId: activityId
            }
            _self.A.post("/v1/attendance/detail",submitData).then(function(result){
              if(result.data.state.code == 0) {
                wechatShareData = result.data.data.attendance;
                resolve();
              } else {

              }
            });
          });
          //,
          if(_self.punchData){
            _self.H.toAjax(true).then(function (data) {
              var submitData = data;
              var activityId = _self.$route.query.id;
              submitData.data = {
                activityId: activityId
              }
              _self.A.post("/v1/attendance/detail",submitData).then(function(result){
//                console.log("ti"+result.data.state.code);
                if(result.data.state.code == 0) {
                  wechatShareData = result.data.data.attendance;
                  resolve();
                } else if(result.data.state.code == 10014){
                  _self.$router.push({path: "/admin/page404"});
                }
              });
            });
          } else {
            wechatShareData = _self.punchData;
            resolve();
          }
        });
      }
      promise().then(function () {
        //
        if(!M.F.D.get("user")) return ;
        //
        _self.H.toAjax(true).then(function (data) {
          console.log("=============info wechat share link====================");
          var config = {
//          debug: true,
            appId: "wx58f3e6eb9f9a1938",
            timestamp : (parseInt((new Date().getTime())/1000)).toString(),
            nonceStr : M.F.getId(16),
          };
          if(M.isQC || M.isDev){
            config.appId = M.appId
          }

          var configData = {
            noncestr : config.nonceStr,
            timestamp: config.timestamp,
            url: window.location.href.split('#')[0]
          };

          var submitData = data;
          submitData.data = configData;
          //
          _self.A.post("/v1/common/configSign",submitData).then(function(result){
            config.signature = result.data.data.sign ;
            return config;
          }).then(function (result) {
            var promise = function () {
              return new Promise(function (resolve, reject) {
                var config = result;

                config.jsApiList = [
                  'hideAllNonBaseMenuItem',
                  'onMenuShareTimeline',
                  'onMenuShareAppMessage'
                ];

                _self.$wechat.config({
//                  debug: true,
                  appId: config.appId,
                  timestamp: config.timestamp,
                  nonceStr: config.nonceStr,
                  signature: config.signature,
                  jsApiList: config.jsApiList
                });

                //userCode +  +  +  +  + 
                resolve(_self.A.post("/v1/userbind/userCode",submitData));

              });
            };

            promise().then(function (userCodeResult) {
              var userCode = userCodeResult.data.data.userCode;
              var title = ''; // 
              wechatShareData.name ? title = wechatShareData.name : title = "";
              var desc = "" + wechatShareData.authNum + "" + title + ",!"; // 
              var imgUrl = _self.baseUrl + wechatShareData.logoImg.url;
              var link = window.location.origin +
                '/user/punchShow/info?id=' +
                _self.$route.query.id +
                '&type=' +
                _self.$route.query.type +
                '&userCode=' +
                userCode;

              console.log("wechatShareData",wechatShareData);

              _self.$wechat.ready(function () {
                //
                _self.$wechat.onMenuShareTimeline({
                  title: title, // 
                  link: link, // 
                  imgUrl: imgUrl, // 
                  success: function () {
                    // 
                  },
                  cancel: function () {
                    // 
                  }
                });
                //
                _self.$wechat.onMenuShareAppMessage({
                  title: title, // 
                  desc: desc, // 
                  link: link, // 
                  imgUrl: imgUrl, // 
                  type: 'link', // ,musicvideolinklink
                  dataUrl: '', // typemusicvideo
                  success: function () {
                    // 
                  },
                  cancel: function () {
                    // 
                  }
                });
              });

              _self.$wechat.error(function (error) {
                console.log(error);
              });
            });

          });

        });
      });
    },
    mounted(){
      document.getElementById("title").innerHTML = "";
      this.$emit("updatePunch");
      this.$emit("isInfo",true);
      this.$emit("isMeet");//set false
      var _id = this.activityId = this.section.activityId = this.$route.query.id ;
      $sectionList.call(this,_id);

      //userCode
      var _self = this;
      this.H.toAjax(true).then(function(comData) { //data 
        var data = {
          id: _self.$route.query.id,
          type: _self.$route.query.type
        }
        comData.data = data;
        _self.A.post("/v1/userbind/clientUserCode", comData)
          .then(function (result) {
            if(result.status == 0){
              M.F.D.set("userCode",result._data.userCode);
              M.userCode = result._data.userCode;
            }
          });
      });
